import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import Tree from 'react-d3-tree';
import './custom_tree.css'

interface RawNodeDatum {
  name: string;
  attributes?: Record<string, string | number | boolean>;
  children?: RawNodeDatum[];
}

const default_sep= {siblings:2,nonSiblings:2}

class ParseTreeComponent extends StreamlitComponentBase {
  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`. Here, we access the "name" arg.
    const data = this.props.args["data"]
    const custom_style=this.props.args['style']
    //need to make a converter for input to acceptable output
    // input: parseTree from constituency parser
    // output: sth like orgChart
    const { theme } = this.props
    const style: React.CSSProperties = {}

    return (
      <div id="treeWrapper" style={custom_style}>
          <Tree data={data}
          orientation='vertical' 
          separation={default_sep}
          rootNodeClassName="node__root"
          branchNodeClassName="node__branch"
          leafNodeClassName="node__leaf"
          />
      </div>
    )
  }


}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(ParseTreeComponent)
